:root{--f: #161616;--b: #ffffff;--g: rgb(0, 200, 5);--r: rgb(255, 80, 0)}html{font-size:16px}body{font-family:"Source Sans Pro",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;font-size:100%;color:var(--f);background-color:var(--b);margin:0;padding:0;transition:color 100ms ease-in-out,background-color 100ms ease-in-out}body.stonks{--f: #ffffff;--b: #000000}